import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Menu } from "antd";
import type { ItemType } from "antd/lib/menu/hooks/useItems";
import { listToTree } from "@/utils/util";
import { MenuItemType, MenuInfoType } from "@/interface/common";
import MyIcon from "@/components/MyIcon";
import routeList from "@/common/menuList";

const treeToList = (tree: any[], result: MenuItemType[] = [], level = 1) => {
  tree.forEach((node) => {
    const menuItem: MenuItemType = {
      key: node.action,
      label: node.name,
      id: node.id,
      pid: node.parent_id,
    };
    if (level < 2) {
      menuItem.icon = <MyIcon icon={node.icon} />;
    }
    result.push(menuItem);
    if (level === 1 && node?.children?.length > 0) {
      treeToList(node.children, result, level + 1);
    }
  });
  return result;
};

const GlobalSideBar = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { pathname } = location;
  const userInfoStr = localStorage.getItem("userInfo");
  const [menuList, setMenuList] = useState<ItemType[]>([]);
  const [selectedKeys, setSelectedKeys] = useState<string[]>([]);
  const [openKeys, setOpenKeys] = useState<string[]>([]);

  useEffect(() => {
    if (userInfoStr) {
      const { permissions = [] } = JSON.parse(userInfoStr || "{}");
      const flatArr = treeToList(permissions);
      const handleMenuList = listToTree(flatArr);
      setMenuList(handleMenuList);
      if (pathname === "/" && handleMenuList?.length > 0) {
        navigate(handleMenuList[0].children[0].key);
        setOpenKeys([handleMenuList[0].key]);
        setSelectedKeys([handleMenuList[0].children[0].key]);
      }
      if (pathname !== "/") {
        const splitPathName = pathname.split("/");
        setSelectedKeys([`/${splitPathName[1]}/${splitPathName[2]}`]);
        const openKey = `/${splitPathName[1]}`;
        setOpenKeys([openKey]);
      }
    } else {
      const flatArr = treeToList(routeList);
      const handleMenuList = listToTree(flatArr);
      setMenuList(handleMenuList);
      if (pathname === "/" && handleMenuList?.length > 0) {
        navigate(handleMenuList[0].children[0].key);
        setOpenKeys([handleMenuList[0].key]);
        setSelectedKeys([handleMenuList[0].children[0].key]);
      }
      if (pathname !== "/") {
        const splitPathName = pathname.split("/");
        setSelectedKeys([`/${splitPathName[1]}/${splitPathName[2]}`]);
        const openKey = `/${splitPathName[1]}`;
        setOpenKeys([openKey]);
      }
    }
  }, [userInfoStr, pathname, navigate]);

  const handleClickMenuItem = (menuItem: MenuInfoType) => {
    const { key } = menuItem;
    navigate(key);
    setSelectedKeys([key]);
  };

  const handleOpenChange = (keys: string[]) => {
    const latestOpenKey = keys.find((key) => openKeys.indexOf(key) === -1);
    if (latestOpenKey) setOpenKeys([latestOpenKey]);
  };

  return (
    <Menu
      onOpenChange={handleOpenChange}
      onClick={handleClickMenuItem}
      selectedKeys={selectedKeys}
      openKeys={openKeys}
      mode='inline'
      items={menuList}
      expandIcon={({ isOpen }) => (
        <span style={{ transform: isOpen ? "scaleY(-1)" : "none" }}>
          <MyIcon icon='icon-a-icon_downarrow' height={8} width={8} />
        </span>
      )}
    />
  );
};

export default GlobalSideBar;
