  const location = useLocation();

  const getFlatRouteList = (routeList: RouteType[], flatRouteList: any[]) => {
    routeList?.forEach((route) => {
      if (route?.children?.length > 0) {
        getFlatRouteList(route.children, flatRouteList);
      }
      flatRouteList.push({
        id: route.id,
        action: route.action,
        children: route.children,
      });
    });
  };

  const setRouteId = () => {
    const route = location.pathname;
    if (route) {
      const routeArr = route.split("/");
      if (routeArr?.length > 0) {
        const routeList = JSON.parse(localStorage.getItem("userInfo") || "{}").permissions || menuList;
        const flatRouteList = JSON.parse(sessionStorage.getItem("flatRouteList") || "[]");
        if (flatRouteList.length === 0) {
          getFlatRouteList(routeList, flatRouteList);
          sessionStorage.setItem("flatRouteList", JSON.stringify(flatRouteList));
        }
        flatRouteList.forEach((item: RouteType) => {
          let actionExcludeParams = item.action;
          let routeExcludeParams = route;
          if (actionExcludeParams.indexOf(":") > -1) {
            actionExcludeParams = item.action.substring(0, item.action.indexOf(":"));
            routeExcludeParams = route.substring(0, actionExcludeParams.length);
          }
          if (item.action.split("/").length === route.split("/").length && actionExcludeParams === routeExcludeParams) {
            sessionStorage.setItem("buttonList", JSON.stringify(item?.children?.length > 0 ? item.children : []));
          }
        });
      }
    }
  };

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  }, []);

