import React from "react";
import { Breadcrumb } from "antd";
import { Link, useLocation, useParams } from "react-router-dom";
import styles from "@/layout/BasicLayout/index.module.less";
import MyIcon from "@/components/MyIcon";
import menuList from "@/common/menuList";

function treeFindPath(tree: any[], routerNameArr: any[], pathnameArr: any[]) {
  if (tree?.length > 0) {
    tree.forEach((item) => {
      if (item.action.indexOf(pathnameArr[0]) > -1) {
        const hasChildren = item.children?.length > 0 ? 1 : 0;
        routerNameArr.push({ path: item.action, name: item.name, hasChildren, icon: item.icon });
        pathnameArr.shift();
        if (item?.children?.length > 0 && pathnameArr?.length > 0) {
          treeFindPath(item.children, routerNameArr, pathnameArr);
        }
      }
      if (item.action === "/") {
        if (item?.children?.length > 0 && pathnameArr?.length > 0) {
          treeFindPath(item.children, routerNameArr, pathnameArr);
        }
      }
    });
  }
}

const MyBreadcrumb = () => {
  const { pathname = "" } = useLocation();
  const params = useParams();
  const userInfoStr = localStorage.getItem("userInfo");
  let routerList = menuList || [];
  if (userInfoStr) {
    routerList = JSON.parse(userInfoStr || "{}").permissions;
  }

  const routerNameArr: any[] = [];
  if (pathname) {
    const pathnameArr = pathname.substring(1).split("/");
    treeFindPath(routerList, routerNameArr, pathnameArr);
  }

  const setPathParams = (path: any) => {
    let resultPath = path;
    Object.keys(params).forEach((key) => {
      resultPath = resultPath.replace(`:${key}`, params[key]);
    });
    return resultPath;
  };

  return (
    <div className={styles["global-breadcrumb"]}>
      <Breadcrumb>
        {routerNameArr?.length > 0 ? (
          <Breadcrumb.Item>
            <span style={{ position: "relative", bottom: 4 }}>
              <MyIcon icon={routerNameArr[0].icon} />
            </span>
          </Breadcrumb.Item>
        ) : null}
        {routerNameArr.map((item, index) => (
          <Breadcrumb.Item key={item.path}>
            {routerNameArr?.length > 2 && index >= 1 ? (
              <span style={{ cursor: "pointer" }}>
                <Link to={setPathParams(item.path)}>{item.name}</Link>
              </span>
            ) : (
              item.name
            )}
          </Breadcrumb.Item>
        ))}
      </Breadcrumb>
    </div>
  );
};

export default MyBreadcrumb;
