import React from "react";
import { useNavigate } from "react-router-dom";
import { Result, Button } from "antd";

const NotFound = () => {
  const navigate = useNavigate();
  const back = () => {
    navigate(-1);
  };
  return (
    <Result
      status='404'
      title='404'
      subTitle='此页面未找到'
      extra={(
        <Button type='primary' onClick={back}>
          返回
        </Button>
      )}
    />
  );
};

export default NotFound;
