import React from "react";
import { useNavigate } from "react-router-dom";
import { Form, Button, Input } from "antd";
import { useStores } from "../../hooks/use-stores";
import styles from "./style.module.less";

const FormItem = Form.Item;

const Login = () => {
  const [form] = Form.useForm();
  const navigate = useNavigate();
  const { commonStore, userStore } = useStores();

  const handleSubmit = () => {
    form.validateFields().then(async (values) => {
      commonStore.outsiderLogin({ ...values, password: btoa(values.password) }).then(() => {
        if (commonStore.tokenStr) {
          commonStore.getCommonEnum();
          userStore.getUserInfo({ is_update: 1 }).then(() => {
            navigate("/");
          });
        }
      });
    });
  };

  return (
    <div>
      <div className={styles.loginBox}>
        <div className={styles.loginTitle}>登录</div>
        <Form onFinish={handleSubmit} className={styles.loginForm} name='login' form={form}>
          <FormItem
            name='username'
            rules={[
              {
                required: true,
                message: "请输入账号！",
              },
            ]}
          >
            <Input autoComplete='off' className={styles.loginInput} placeholder='邮箱' />
          </FormItem>
          <FormItem
            name='password'
            rules={[
              {
                required: true,
                message: "请输入密码！",
              },
            ]}
          >
            <Input autoComplete='off' type='password' className={styles.loginInput} placeholder='密码' />
          </FormItem>
          <FormItem>
            <Button type='primary' style={{ marginTop: 20 }} htmlType='submit' className={styles.loginFormButton}>
              登录
            </Button>
          </FormItem>
          <div className={styles.tip}>如忘记账户密码，请联系管理员</div>
        </Form>
      </div>
      <div className={styles.loginLeft}>
        <div className={styles.companyIntroduction}>
          <div className={styles.contentTitle}>
            <span>车型搭载项目平台</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
