import { runInAction } from "mobx";
// import {
//   getOutsiderLoginApi,
//   getSsoLoginApi,
//   getSsoLogoutApi,
//   getTokenApi,
//   getCommonEnumApi,
// } from "../constants/api/commonApi";
// import { appBaseUrl } from '../util/http';
// import { SSO_LOGIN } from '../util/constant';

const CommonStore = () => ({
  tokenStr: "",
  enumObj: {}, // 获取对应模块枚举类时 使用enumObj.模块名?.枚举类  建议带上？ 防止undefined造成代码报错
  getSignature() {
    let _signatureStr = "";
    if (window.location.href && window.location.href.indexOf("?signature=") === -1) {
      window.location.href = getSsoLoginApi;
      return false;
    }
    _signatureStr = window.location.search.split("?signature=")[1];
    localStorage.setItem("signature", _signatureStr);
    return _signatureStr;
  },
  getCommonEnum() {
    getCommonEnumApi()
      .then((res) => {
        this.enumObj = res;
        localStorage.setItem("enumObj", JSON.stringify(this.enumObj));
      })
      .catch((err) => {
        console.info(err);
      });
  },
  async getTokenStr() {
    try {
      const data = {};
      if (import.meta.env.VITE_APP_ENV === "dev") {
        const signature = this.getSignature();
        data.signature = signature;
      }
      if (data?.signature || import.meta.env.VITE_APP_ENV !== "dev") {
        const res = await getTokenApi(data);
        console.log(`getTokenStr:${JSON.stringify(res)}`);
        if (res) {
          runInAction(() => {
            this.tokenStr = res;
            localStorage.setItem("token", this.tokenStr);
          });
        }
      }
    } catch (err) {
      console.log(`getTokenStr fail:${JSON.stringify(err)}`);
      await Promise.reject(err);
    }
  },

  async outsiderLogin(data) {
    try {
      const res = await getOutsiderLoginApi(data);
      console.log(`getTokenStr:${JSON.stringify(res)}`);
      if (res) {
        runInAction(() => {
          this.tokenStr = res;
          localStorage.setItem("token", this.tokenStr);
        });
      }
    } catch (err) {
      console.log(`outsiderLogin fail:${JSON.stringify(err)}`);
      await Promise.reject(err);
    }
  },

  async ssoLogoutApi(data) {
    try {
      const res = await getSsoLogoutApi(data);
      if (res) {
        runInAction(() => {
          this.tokenStr = "";
          localStorage.clear();
          sessionStorage.clear();
        });
      }
    } catch (err) {
      console.log(`ssoLogoutApi fail:${JSON.stringify(err)}`);
      await Promise.reject(err);
    }
  },
});
export default CommonStore;
