import { MyIconType } from "@/interface/common";

const MyIcon = ({ icon, color = "", width = 16, height = 16, style }: MyIconType) => {
  return (
    <svg className='icon' aria-hidden='true' style={style}>
      <use xlinkHref={`#${icon}`} color={color} width={width} height={height} />
    </svg>
  );
};

export default MyIcon;
