import React from "react";
import locale from "antd/es/locale/zh_CN";
import "moment/dist/locale/zh-cn";
import { HashRouter as Router } from "react-router-dom";
import { ConfigProvider } from "antd";
import { useLocalObservable } from "mobx-react";
import { publicStores } from "../hooks/use-stores";
import { PREFIX_CLS } from "@/constants/constant";
import storesContext from "../store";
import App from "./App";

// 全局配置antd弹窗框类组件渲染父节点
const getPopupContainer = (node: HTMLElement | undefined): HTMLElement => {
  if (node?.className.includes(`${PREFIX_CLS}-select-selector`)) {
    return node.parentNode as HTMLElement;
  }
  return document.body;
};

function Main() {
  const usedStore = useLocalObservable(() => publicStores());
  // TODO
  if (typeof window.global === "undefined") {
    window.global = window;
  }
  ConfigProvider.config({ prefixCls: PREFIX_CLS });
  return (
    <storesContext.Provider value={usedStore}>
      <Router>
        <ConfigProvider
          locale={locale}
          prefixCls={PREFIX_CLS}
          getPopupContainer={getPopupContainer}
        >
          <App />
        </ConfigProvider>
      </Router>
    </storesContext.Provider>
  );
}

export default Main;
