import React from "react";
import { RouteObject } from "react-router-dom";
import { ViteFilesType } from "@/interface/common";
import BasicLayout from "../layout/BasicLayout";
import NotFound from "../pages/NotFound";
import Login from "../pages/Login";

const routerArr: RouteObject[] = [];
const files = Object.values(import.meta.glob("./routes/*.tsx", { eager: true })) as ViteFilesType[];

files.forEach((item) => {
  routerArr.push(item.default || []);
});

const routerList = [
  {
    path: "/",
    exact: true,
    element: <BasicLayout />,
    children: [
      ...routerArr,
      {
        path: "/NotFound",
        name: "404",
        element: <NotFound />,
      },
      {
        path: "/*",
        name: "404",
        element: <NotFound />,
      },
    ],
  },
  {
    path: "/Login",
    name: "登录",
    element: <Login />,
  },
];

export default routerList;
