import React from "react";
import { useNavigate } from "react-router-dom";
import { Result, Button } from "antd";

const Wrong = () => {
  const navigate = useNavigate();
  const back = () => {
    navigate(-1);
  };
  return (
    <Result
      status='500'
      title='500'
      subTitle='服务器发生了错误'
      extra={
        <Button type='primary' onClick={back}>
          返回
        </Button>
      }
    />
  );
};

export default Wrong;
