import React from "react";
import Wrong from "../Result/Wrong";
import { propType, StateType } from "@/interface/errorBoundary";

class ErrorBoundary extends React.Component<propType, StateType> {
  // 处理错误边界，子组件报错会执行
  static getDerivedStateFromError(error: { name: string }) {
    // 处理重新发布后，未重新打开页面，操作之前打开的页面报错
    // TODO 待测试
    if (error.name === "ChunkLoadError") {
      window.location.reload();
    } else {
      return { hasError: true };
    }
    return { hasError: false };
  }

  constructor(props: propType | Readonly<propType>) {
    super(props);
    this.state = {
      hasError: false,
    };
  }

  componentDidUpdate(prevProps: any, prevState: { hasError: boolean }) {
    // 切换路由时，不影响别的页面
    if (prevState.hasError) {
      this.setState({ hasError: false });
    }
  }

  // componentDidCatch(error, info) {
  //   // 错误上报
  //   console.log({ error, info });
  // }

  render() {
    const { hasError } = this.state;
    return hasError ? <Wrong /> : this.props?.children;
  }
}

export default ErrorBoundary;
