/**
 *  TOKEN错误状态码
 *  40000: 签名解析失败【签名作为参数附在登录成功后的跳转链接中,key是"signature"】
 *  40001: 表示签名已过期
 *  40002: 表示用户信息不存在
 *  40003: 表示缺少token【token放在请求header中，key是"Token"】
 *  40004: 表示用户登录已过期
 *  40005: 表示token解析错误
 *  40006: 表示缺少前端router【router放在请求header中，key是"Router"】
 *  40007: 表示无权限
 * */
export const TOKEN_CODE_ERROR_LIST = [40000, 40001, 40002, 40003, 40004, 40005, 40006, 40007];

