// 判断车架号是否合法
export const verificationVIN = (vin: string) => {
  // 特殊符号正则
  const regEn = /[`~!@#$%^&*()_+<>?:"{},./;'[\]]/im;
  const regCn = /[·！#￥（—）：；“”‘、，|《。》？【】[\]]/im;
  const regNumLetter = /[^\w/]/gi;

  if (regEn.test(vin) || regCn.test(vin) || regNumLetter.test(vin)) {
    errorToast({ msg: "查询条件不能包含特殊符号或汉字" });
    return false;
  }
  if (vin === null || vin === undefined || vin === "") {
    errorToast({ msg: "查询条件不可为空" }).then();
    return false;
  }
  if (vin.length !== 17) {
    errorToast({ msg: "请输入17位车架号" });
    return false;
  }
  return true;
};

// 隐藏跟页面滚动条
export const alertNoScroll = () => {
  let top = 0;
  if (document.documentElement.scrollTop) {
    top = document.documentElement.scrollTop;
  } else if (document.body.scrollTop) {
    top = document.body.scrollTop;
  }
  document.body.style.cssText = `width: 100%; height: 100%; position: fixed; top: -${top}px; left: 0; overflow: hidden;`;
};

// 显示根页面滚动条
export const cancelAlert = () => {
  document.body.removeAttribute("style");
  document.body.scrollTop = document.documentElement.scrollTop
    ? document.documentElement.scrollTop
    : document.body.scrollTop;
};

// 时间转换
export function ConverToDate(DateStr: string) {
  if (DateStr && typeof DateStr === "string") {
    return DateStr.replace(/-/g, "/");
  }
}

// 防抖
export function debounce(fn: { apply: (arg0: any, arg1: any[]) => void }, wait: number | undefined, promptly: boolean) {
  let timerID: NodeJS.Timeout | null | undefined;
  return function init(this: any, ...args: any[]) {
    const context = this;
    if (timerID) clearTimeout(timerID);
    if (promptly) {
      const callNow = !timerID;
      timerID = setTimeout(() => {
        timerID = null;
      }, wait);
      if (callNow) fn.apply(context, [...args]);
    } else {
      timerID = setTimeout(() => {
        fn.apply(context, [...args]);
      }, wait);
    }
  };
}

export const getPathById = (catalog: any, id: any, callback: (arg0: any[]) => void) => {
  // 定义变量保存当前结果路径
  const tempPath: any[] = [];
  try {
    const getNodePath = (node: { id: any; children: string | any[] }) => {
      tempPath.push(node.id);

      // 找到符合条件的节点，通过throw终止掉递归
      if (node.id === id) {
        const errorMessage = { message: "GOT IT!" };
        throw errorMessage;
      }
      if (node.children && node.children.length > 0) {
        for (let i = 0; i < node.children.length; i++) {
          getNodePath(node.children[i]);
        }
        // 当前节点的子节点遍历完依旧没找到，则删除路径中的该节点
        tempPath.pop();
      } else {
        // 找到叶子节点时，删除路径当中的该叶子节点
        tempPath.pop();
      }
    };
    getNodePath(catalog);
  } catch (e) {
    const result = tempPath;
    callback(result);
  }
};

// 使用随机数
/* eslint no-bitwise: ["error", { "allow": ["|"] }] */
const S4 = () => (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
// 调用guid函数生成uid
export const guid = () => `${S4()}${S4()}-${S4()}-${S4()}-${S4()}-${S4()}${S4()}${S4()}`;

/**
 * 下载文件
 * @param url 下载文件接口地址
 * @param name 自定义下载文件名称
 */
export const downloadFile = (url: string | URL, name: string) => {
  console.log(url);
  const xhr = new XMLHttpRequest();
  xhr.open("GET", url, true);
  xhr.setRequestHeader("token", localStorage.getItem("token") || "");
  xhr.responseType = "blob";
  xhr.onload = () => {
    if (xhr.readyState === 4 && xhr.status === 200) {
      const blob = xhr.response;
      const u = window.URL.createObjectURL(new Blob([blob]));
      const a = document.createElement("a");
      a.download = name;
      a.href = u;
      a.style.display = "none";
      document.body.appendChild(a);
      a.click();
      a.remove();
    }
  };
  xhr.send();
};

export const deepClone = (target: any[] | null, map = new Map()) => {
  if (target !== null && typeof target === "object") {
    // 从缓存容器中读取克隆对象
    let cloneTarget = map.get(target);
    // 如果存在, 返回前面缓存的克隆对象
    if (cloneTarget) {
      return cloneTarget;
    }
    // 创建克隆对象(可能是{}或者[])
    if (target instanceof Array) {
      cloneTarget = [];
      // 缓存到map中
      map.set(target, cloneTarget);
      target.forEach((item, index) => {
        cloneTarget[index] = deepClone(item, map);
      });
    } else {
      cloneTarget = {};
      // 缓存到map中
      map.set(target, cloneTarget);
      Object.keys(target).forEach((key) => {
        cloneTarget[key] = deepClone(target[key], map);
      });
    }

    return cloneTarget;
  }

  return target;
};

export const isValidKey = (key: string | number | symbol, object: object): key is keyof typeof object => {
  return key in object;
};

export const getUrlData = () => {
  const urlArr = window.location.hash.split("?")[1].split("&");
  const modeObj = {};
  urlArr.forEach((item) => {
    const name = item.substring(0, item.indexOf("="));
    let value = item.substring(item.indexOf("=") + 1);
    value = decodeURIComponent(value);
    if (isValidKey(name, modeObj)) {
      (modeObj[name] as string) = value;
    }
  });
  return modeObj;
};

export const canvasWM = ({
  container = document.body,
  width = "400px",
  height = "200px",
  textAlign = "center",
  textBaseline = "middle",
  font = "300 12px Microsoft YaHei",
  fillStyle = "rgba(220,222,226,0.64)",
  content = "水印",
  rotate = -30,
  zIndex = 9999,
}: CanvasWMType) => {
  const canvas = document.createElement("canvas");
  canvas.setAttribute("width", width);
  canvas.setAttribute("height", height);
  const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
  ctx.textAlign = textAlign;
  ctx.textBaseline = textBaseline;
  ctx.font = font;
  ctx.fillStyle = fillStyle;
  ctx.rotate((Math.PI / 180) * rotate);
  ctx.fillText(content, 100, 100);
  ctx.fillText(moment().format(DATE_TIME_FORMAT), 100, 120);
  const base64Url = canvas.toDataURL();
  const __wm = document.querySelector(".__wm");
  const watermarkDiv = __wm || document.createElement("div");
  const styleStr = `
                  position:fixed;
                  top:0;
                  left:0;
                  bottom:0;
                  right:0;
                  width:100%;
                  height:100%;
                  z-index:${zIndex};
                  pointer-events:none;
                  background-repeat:repeat;
                  background-image:url('${base64Url}')`;

  watermarkDiv.setAttribute("style", styleStr);
  watermarkDiv.classList.add("__wm");
  if (!__wm) {
    container.insertBefore(watermarkDiv, container.firstChild);
  }
};

export const removeCanvasWM = () => {
  const self = document.getElementsByClassName("__wm");
  if (self?.length > 0) {
    const parent = self[0].parentElement as HTMLElement;
    parent.removeChild(self[0]);
  }
};

export const listToTree = (list: any[], id = "id", pid = "pid") => {
  list.forEach((node: { [x: string]: any }) => {
    const pNode = list.find((row: { [x: string]: any }) => row[id] === node[pid]);
    if (pNode) {
      pNode.children = pNode.children || [];
      pNode.children.push(node);
    }
  });
  return list.filter((node: { [x: string]: any }) => !node[pid]);
};

export const getAvatarBackgroundColor = () => {
  const avatarBackgroundColor = ["#003FBC", "#00A262", "#425473", "#EAAD00", "#DD4C38", "#0076A5"];
  return avatarBackgroundColor[Math.round(Math.random() * 5)];
};

// 获取列表id
export const getListDataId = (data: any[], rowKey = "id", results: Array<string | number> = []) => {
  data.forEach((item: { [x: string]: any; children: any }) => {
    if (item.children) {
      results.push(item[rowKey]);
      getListDataId(item.children, rowKey, results);
    }
  });
  return results;
};

/**
 * 处理store
 * @param api 调用后端api接口
 * @param apiParams 接口需要传递的参数
 * @param loading 涉及到的loading变量，使用字符串
 * @param result 需要修改的结果值，使用字符串
 * @param msg 调用接口提示
 * @returns {Promise<unknown>}
 */
export async function setResult(this: any, { api, apiParams, loading, result, msg }: SetResultType) {
  if (loading) this[loading] = true;
  try {
    const res = await api(apiParams);
    runInAction(() => {
      if (loading) this[loading] = false;
      if (result) this[result] = res;
    });
    if (msg) {
      successToast({ msg });
    }
    return Promise.resolve(res);
  } catch (e) {
    runInAction(() => {
      if (loading) this[loading] = false;
    });
    return Promise.reject(e);
  }
}

// 数组排序
export function compare(property: string | number) {
  return function (a: { [x: string]: any }, b: { [x: string]: any }) {
    const value1 = a[property];
    const value2 = b[property];
    return value1 - value2;
  };
}

/**
 *
 * @param params 页面setParams保存的查询条件
 * @param queryList 筛选组件返回的查询条件
 * @returns
 */
export const sortingParams = (params: any, queryList: any) => {
  const results = { ...params, ...queryList, page: 1 };
  Object.keys(results).forEach((item) => {
    if (results[item] !== 0 && !results[item]) {
      delete results[item];
    }
  });
  return results;
};
