import React, { lazy } from "react";
import { DynamicRoutesType, RouterListType } from "@/interface/common";
import withRouterGuard from "../common/hoc/withRouterGuard";

/**
 * 根据组件路径生成路由配置文件
 * @param files
 * @param dynamicRoutes 需要动态参数的路由
 */
const generateRouterList = (files: any[], dynamicRoutes: DynamicRoutesType[] = []) => {
  const childrenList: RouterListType[] = [];

  files.forEach((item) => {
    const SpecificStory = withRouterGuard(lazy(item));

    const filePath = item.name;
    const routePath = filePath.replace(
      /(\.\.\/|\.\/|(src|\/src)|(pages|\/pages)|(components|\/components)|\/index\.tsx)/g,
      "",
    );
    const hasDynamicParams = dynamicRoutes.find((route) => routePath === route.path);
    const path = `${routePath}${hasDynamicParams ? hasDynamicParams.params.join("") : ""}`;

    childrenList.push({
      path,
      element: <SpecificStory path={path} />,
    });
  });

  return childrenList;
};

export default generateRouterList;
