import { runInAction } from "mobx";
// import { getUserInfoApi, getUserPermissionApi } from "../constants/api/userApi";

const UserStore = () => ({
  userInfoObj: {
    permissions: [],
    roles: [],
    user_info: {},
    name: null,
    work_no: null,
  },
  permission: [],

  async getUserInfo(data) {
    try {
      const res = await getUserInfoApi(data);
      if (res) {
        runInAction(() => {
          this.userInfoObj = res;
          localStorage.setItem("userInfo", JSON.stringify(this.userInfoObj));
        });
      }
    } catch (err) {
      console.log(`getUserInfo fail: ${JSON.stringify(err)}`);
    }
  },

  async getUserPermission(data) {
    try {
      const res = await getUserPermissionApi(data);
      if (res) {
        runInAction(() => {
          this.permission = res;
          localStorage.setItem("permission", JSON.stringify(this.permission));
        });
      }
    } catch (err) {
      console.log(`getUserPermission fail: ${JSON.stringify(err)}`);
    }
  },

  resetUserInfo() {
    runInAction(() => {
      this.userInfoObj = { permissions: [], roles: [], user_info: {} };
    });
  },
});
export default UserStore;
