import React, { Suspense } from "react";
import { RouterPropType } from "@/interface/common";
import NotFound from "../../pages/NotFound";

const withRouterGuard = (WrappedComponent: React.LazyExoticComponent<React.ComponentType<any>>) => {
  return class extends React.Component<RouterPropType> {
    constructor(props: RouterPropType | Readonly<RouterPropType>) {
      super(props);
      this.state = {};
    }

    render() {
      const { path } = this.props;
      const flatRouteList = JSON.parse(sessionStorage.getItem("flatRouteList") || "[]");
      const index = flatRouteList.findIndex((item: { action: any; }) => item.action === path);
      if (index !== -1) {
        return (
          <Suspense>
            <WrappedComponent {...this.props} />
          </Suspense>
        );
      }
      return <NotFound />;
    }
  };
};

export default withRouterGuard;
